}

/* /////////////////////////// */

.post-form {
	padding: 15px;
}

.post-form form {
	display: flex;
	gap: 10px;
}

.post-form button {
	min-width: 100px;
	border: none;
	border-radius: 20px;
	padding: 10px;
	background-color: #1df05c;
	color: white;
	font-weight: 800;
}

.post-form button:hover {
	background-color: #1ad251;
}

.post-form input {
	width: 300px;
	border-radius: 20px;
	background-color: #7a5059;
	border: none;
	align-self: center;
	padding: 10px;
	color: white;
}

/* /////////////////////////// */

.homepage-posts {
	width: 725px;
	box-sizing: border-box;
	border: 1px solid rgba(184, 184, 184, 0.3);
	background-color: #4d3238;
	box-shadow: 3px 2px 5px 1px rgba(0, 0, 0, 0.5);
}

.homepage-posts h1 {
	padding: 15px;
	padding-bottom: 0;
}

.homepage-post {
	display: flex;
	flex-direction: column;
	padding: 18px;
	padding-bottom: 10px;
	border: 1px solid rgba(184, 184, 184, 0.3);
	border-left: none;
	border-right: none;
	min-height: 135px;
	height: fit-content;
}

.homepage-post > p {
	overflow: hidden;
	overflow-wrap: break-word;
}

.post-title {
	display: flex;
	gap: 5px;
}

.post-title h4 {
	font-size: 16px;
}

.post-title p {
	font-size: 16px;
	font-weight: 600;
	color: rgb(145, 145, 145);
}

.post-footer,
.post-comment-footer {
	display: flex;
	gap: 10px;
	align-items: center;
	justify-content: space-around;
	margin-top: auto;
}

/* /////////////////////////// */

.homepage-sidebar {
	display: flex;
	flex-direction: column;
	gap: 12px;

	position: sticky;
	top: 0;

	max-width: 200px;
	height: 100vh;
	min-height: fit-content;
	padding: 20px;
	background-color: #422b30;
}

.homepage-sidebar h2 {
	overflow: hidden;
	overflow-wrap: break-word;
}

.homepage-sidebar button {
	min-width: 100px;
	width: 100%;

	border: none;
	border-radius: 20px;
	padding: 10px;

	background-color: white;
	font-weight: 800;
}

.homepage-sidebar button:hover {
	background-color: rgb(214, 214, 214);
}

.homepage-sidebar a {
	text-decoration: none;
	color: inherit;
}
